import { 
  type ChangeEvent,
  type FC,
  createRef,
  useEffect,
  useMemo,
  useState 
} from 'react'
import Input from '../'
import type { InputProps } from '../types'

export { Input }

export const InputExampleHOC = (hocProps: InputProps) =>
  (props: InputProps) => (<Input {...hocProps} {...props} />)

export const DefaultExample = InputExampleHOC({
  id: 'default.example'
})

export const DefaultWithPlaceholderExample = InputExampleHOC({
  id: 'default.with.placeholder.example',
  placeholder: 'your input goes here'
})

export const InputWithLabelExample = InputExampleHOC({
  id: 'input.with.label.example',
  label: 'Email'
})

export const InputWithDefaultValueExample = InputExampleHOC({
  id: 'input.with.default.value.example',
  label: 'Email',
  defaultValue: 'test@gmail.com'
})

export const InputWithPlaceholderAndLabelExample = InputExampleHOC({
  id: 'input.with.placeholder.and.label.example',
  label: 'Email',
  placeholder: 'your mail here'
})

export const InputWithStartAdornmentAndLabelExample = InputExampleHOC({
  id: 'input.with.start.adornment.and.label.example',
  label: 'Amount',
  startAdornment: '$',
  placeholder: 'exact amount please'
})

export const InputOnlyWithStartAdornmentExample = InputExampleHOC({
  id: 'input.only.with.start.adornment.example',
  startAdornment: '$'
})

export const InputWithBothAdornmentsExample = InputExampleHOC({
  id: 'input.with.both.adornments.example',
  label: 'Amount',
  startAdornment: '$',
  endAdornment: (
    <a
      href='https://google.com'
      rel='noreferrer'
      target='_blank'
      style={{ whiteSpace: 'nowrap' }}
    >
      Show more
    </a>
  )
})

export const InputWithErrorExample = InputExampleHOC({
  id: 'input.with.error.example',
  label: 'Amount',
  startAdornment: '$',
  error: true,
  endAdornment: <span>USD</span>
})

export const InputDisabledExample = InputExampleHOC({
  id: 'input.disabled.example',
  label: 'Amount',
  startAdornment: '$',
  error: true,
  defaultValue: '1902',
  disabled: true,
  endAdornment: <span>USD</span>
})

export const InputWithMaxLengthExample = InputExampleHOC({
  id: 'input.with.max.length.example',
  label: 'Amount',
  startAdornment: '$',
  inputProps: { pattern: '[0-9]+' },
  maxLength: 5,
  defaultValue: '1902',
  endAdornment: <span>USD</span>,
  onKeyPress: (e) => {
    /**
     * if decimals are needed, e.currentTarget.value may be checked
     */
    if (!/[0-9]/.test(e.key)) {
      e.preventDefault()
    }
  }
})

export const InputWithNoCyrillicSymbolsPresetExample = InputExampleHOC({
  id: 'input.with.no.cyrillic.symbols.preset.example',
  preset: 'withoutCyrillic'
})

export const ControlledInputExample: FC = () => {
  const [value, setValue] = useState('')

  const Input = useMemo(
    () =>
      InputExampleHOC({
        id: 'controlled.input.example',
        dataTestId: 'ssn-test-id',
        label: 'SSN',
        startAdornment: '@',
        preset: 'withoutCyrillic'
      }),
    []
  )

  return <Input id='controlled.input.example' value={value} onChange={(e: ChangeEvent<HTMLInputElement>) => setValue(e.target.value)} />
}

export const ControlledByRefInputExample: FC = () => {
  const ref = createRef<HTMLInputElement>()

  const Input = useMemo(
    () =>
      InputExampleHOC({
        id: 'controlled.by.ref.input.example',
        dataTestId: 'controlled-by-ref-input-example',
        inputRef: ref,
        label: 'SSN',
        startAdornment: '@'
      }),
    []
  )

  useEffect(() => {
    setTimeout(() => {
      if (ref.current != null) {
        ref.current.style.borderBottom = '1px dotted green'
      }
    }, 2000)
  }, [])

  return <Input id='controlled.by.ref.input.example'/>
}
