import { GlobalStyles } from '@/UI/GlobalStyles'
import { ChangeEvent, FC, useState } from 'react'
import FormInput from '../FormInput'

export const FormInputExample: FC = () => {
  const [value, setValue] = useState<number | string>(10)
  const [disabled, setDisabled] = useState(false)
  const [touched, setTouched] = useState(false)

  return (
    <div>
      {/* resets some nasty input related styles; such as autosuggestion highlight */}
      <GlobalStyles />
      <button
        style={{ marginRight: '20px' }}
        onClick={() => setDisabled(prev => !prev)}
      >
        Toggle disabled
      </button>
      <button onClick={() => setTouched(false)}>Reset touched</button>
      <hr style={{ margin: '20px 0px' }} />
      <FormInput
        dataTestId='form-input-ssn'
        disabled={disabled}
        error={!/^\d+$/g.test(value.toString())}
        errorMessage={value.toString() === '' ? 'SSN is required' : 'SSN is incorrect'}
        id='ssn'
        helper='Required. Only numbers'
        label='SSN'
        placeholder='special security number'
        startAdornment='@'
        touched={touched}
        value={value}
        onChange={(e: ChangeEvent<HTMLInputElement>) => {
          setValue(e.target.value)
          setTouched(true)
        }}
      />
    </div>
  )
}
